import {upload, createDyn} from '../../api/api.js';
export default {
	data() {
		return {
			imgTemp: this.$store.state.imgTemp,
			dynContent: '',
			location: '所在位置',
			showType: '誰可以看',
			remain: '提醒誰看',
			flog:false
		}
	},
	onNavigationBarButtonTap() {
		if(this.flog){
			return
		}
		this.flog = true
		upload(this.imgTemp).then(data => {
			// console.log(data)
			if(data != '') {
				createDyn(this.dynContent, data).then(data => {
					if(data) {
						uni.navigateTo({
							url: 'daily'
						});
					}
					this.flog = false
				});
			}
			
		});
	}
}
